a {
  text-decoration: none;
  color: #333;
}

.home .static {
  height: 100vh;
  display: flex;
  position: relative;
  flex-direction: column;
}

.home .map {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.home .header {
  display: flex;
  padding: 8px 32px;
}

.home .header .logo {
  flex: 1;
}

.home .header .logo img {
  height: 64px;
}

.home .header .navbar {
  display: flex;
  align-items: center;
}

.home .navbar .nav-item {
  margin-left: 32px;
  font-weight: bold;
  font-size: 0.9em;
  color: #424242;
}

.home .navbar .nav-item.alt {
  font-weight: normal;
}

.home .landing-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.landing-content .heading {
  text-align: center;
}

.landing-content .search-container {
  margin-top: 24px;
  background: #fff;
  border-radius: 10px;
  width: 350px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.landing-content .details {
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.05);
}

.landing-content .detail-item {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
  align-items: center;
  margin-left: 40px;
  padding-right: 16px;
}

.landing-content .detail-item caption {
  font-size: 0.9em;
  color: #787878;
}

.landing-content .detail-item p {
  font-size: 0.9em;
  font-weight: bold;
  color: #555;
}

.search-container .options {
  padding: 8px 0;
}

.search-container .option {
  display: flex;
  padding: 12px 24px;
  align-items: center;
}

.search-container .option .checkbox {
  width: 20px;
  height: 20px;
}

.search-container .option p {
  margin: 0 0 0 16px;
  font-size: 0.9em;
  color: #888888;
}

.landing-content .button {
  background: #4dd960;
  border: none;
  border-radius: 10px;
  text-align: center;
  font-size: 1.2em;
  font-weight: bold;
  color: #fff;
  padding: 12px 16px;
  text-transform: capitalize;
}
